.list {
    $this: &;

    &__item {
        padding-left: 24px;
        position: relative;
        list-style: none;

        &:not(:last-child) {
            margin-bottom: 32px;
        }
        &:has(#{$this}__link--active) {
            &:before {
                content: '';
                left: 0;
                top: 50%;
                width: 4px;
                height: 32px;
                background: #41F6D7;
                position: absolute;
                transform: translate(0, -50%);
                border-top-right-radius: 50%;
                border-bottom-right-radius: 50%;
            }
        }
    }

    &__link {
        cursor: pointer;
        display: block;
        width: 100%;
        transition: .3s;

        &:hover {
            transform: scale(1.08);
        }
        &--active {
            color: #7E7E82;
            font-weight: 500;
            line-height: 24px; /* 150% */
            letter-spacing: -0.2px;
        }
    }
}

